"use client";

interface NavbarProps {
  className?: string;
  showExit?: boolean;
  variant?: "light" | "dark";
  onExitClick?: () => void;
}

export default function Navbar({ className = "", showExit = false, variant = "light", onExitClick }: NavbarProps) {
  const isDark = variant === "dark";
  
  // Figma NavBar_Dark specs:
  // - Background: rgba(199, 201, 206, 0.1)
  // - Backdrop blur: 54px
  // - Border: rgba(255, 255, 255, 0.3) 1px bottom
  // - Text: White (#FFFFFF) 80% opacity
  // - Padding: 18px 32px
  // - Font: SF Pro, 20px, 400 weight, 1.3em line-height

  const textColorClass = isDark 
    ? "text-white/80" 
    : "text-[#03060f]/80";
  
  const borderColorClass = isDark 
    ? "border-white/30" 
    : "border-[#03060f]";

  return (
    <nav 
      className={`
        fixed top-0 left-0 right-0 
        h-[62px] 
        bg-[rgba(199,201,206,0.1)] 
        backdrop-blur-[54px] 
        border-b ${borderColorClass}
        z-50 
        ${className}
      `}
    >
      <div className="w-full h-full px-8 flex items-center justify-between">
        {/* Logo - Figma: SF Pro, 20px, 80% opacity */}
        <span className={`${textColorClass} text-[20px] font-normal font-sf-pro leading-[130%]`}>
          What&apos;s your type?
        </span>
        
        {/* Exit/Share Link - Figma: SF Pro, 20px, 80% opacity */}
        <button 
          onClick={showExit && onExitClick ? onExitClick : undefined}
          className={`${textColorClass} text-[20px] font-normal font-sf-pro leading-[130%] hover:opacity-60 transition-opacity`}
        >
          {showExit ? "Exit" : "Share"}
        </button>
      </div>
    </nav>
  );
}
