"use client";

import { CSSProperties } from "react";

// Map font names to CSS variable names
const fontToCssVar: Record<string, string> = {
  "Alegreya": "var(--font-alegreya)",
  "EB Garamond": "var(--font-eb-garamond)",
  "Libre Baskerville": "var(--font-libre-baskerville)",
  "Playfair Display": "var(--font-playfair-display)",
  "Arvo": "var(--font-arvo)",
  "Source Sans 3": "var(--font-source-sans)",
  "IBM Plex Sans": "var(--font-ibm-plex-sans)",
  "Roboto": "var(--font-roboto)",
  "Montserrat": "var(--font-montserrat)",
  "Open Sans": "var(--font-open-sans)",
  "Lobster": "var(--font-lobster)",
  "Space Mono": "var(--font-space-mono)",
};

interface FontCardProps {
  fontFamily: string;
  sampleText?: string;
  onClick?: () => void;
  isSelected?: boolean;
  className?: string;
  style?: CSSProperties;
}

export default function FontCard({ 
  fontFamily, 
  sampleText = "Handgloves",
  onClick,
  isSelected = false,
  className = "",
  style
}: FontCardProps) {
  const cssFont = fontToCssVar[fontFamily] || fontFamily;
  
  return (
    <button
      onClick={onClick}
      className={`
        bg-[#03060F]
        rounded-[20px]
        flex flex-col
        cursor-pointer
        transition-all duration-300 ease-out
        hover:scale-[1.01]
        active:scale-[0.99]
        ${isSelected ? 'ring-2 ring-white ring-opacity-50' : ''}
        ${className}
      `}
      style={style}
      aria-label={`Select ${fontFamily} font`}
    >
      {/* Font name at the top - 18px, white at 50% opacity */}
      <div className="pt-4 px-4">
        <span className="text-white text-[18px] opacity-50">
          {fontFamily}
        </span>
      </div>
      
      {/* Handgloves text centered in the middle - 60px, responsive */}
      <div className="flex-1 flex items-center justify-center px-4 pb-4">
        <span 
          className="text-white text-[60px] md:text-[clamp(40px,5vw,60px)] text-center leading-tight"
          style={{ fontFamily: cssFont }}
        >
          {sampleText}
        </span>
      </div>
    </button>
  );
}
